/*** Buttons **/
.ant-btn-variant-outlined {
    border-color: #758bfd !important;
    color: #758bfd !important;
}

.ant-btn-variant-link {
    border-color: #000000 !important;
}

.dtm-btn {
    cursor: pointer;
}


/*** Texts **/

.dtm-text {
    color: #758bfd !important;
}

/*** Divs **/
.dtm-elevated {
    background: #fff; /* White background */
    border-radius: 12px; /* Rounded corners */
    padding: 20px; /* Inner padding */
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1), /* Soft outer shadow */ 0px 1px 3px rgba(0, 0, 0, 0.06); /* Slightly sharper inner shadow */
    transition: box-shadow 0.3s ease, transform 0.3s ease; /* Smooth hover effect */
}

.dtm-elevated:hover {
    box-shadow: 0px 6px 12px rgba(0, 0, 0, 0.15), /* Slightly stronger shadow on hover */ 0px 2px 4px rgba(0, 0, 0, 0.1); /* Accent shadow */
    transform: translateY(-2px); /* Lift effect */
}


.merienda-900 {
    font-family: "Merienda", serif;
    font-optical-sizing: auto;
    font-weight: 900;
    font-style: normal;
}
